.about {
  height: 70vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 0.5fr 1.5fr 1fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  grid-template-areas:
    "about__title about__title about__title about__title about__title"
    "about__text about__text about__text about__face-image about__face-image"
    "about__text about__text about__text about__face-image about__face-image";
  color: var(--color-secondary);
  padding: 30px 70px;
}

.about__title {
  grid-area: about__title;
  justify-self: start;
  align-self: end;
}

.about__text {
  grid-area: about__text;
  align-self: center;
}
.about__text P {
  text-align: start;
  margin: var(--spacing-s);
}

.about__face-image {
  grid-area: about__face-image;
  align-self: center;
}

.about__face-image img {
  width: 400px;
}

@media (max-width: 900px) {
  .about{
    display: flex;
    flex-direction: column;
    height: auto;
    padding: 30px 10px;
  }
  .about__face-image img {
    margin-top: 30px;
    width: 250px;
  }
}
.projects {
  background-color: var(--color-primary-alt);
  display: grid;
  grid-gap: 0px 0px;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  color: var(--color-secondary);
  padding: 30px 70px;
}

.projects__title {
  justify-self: start;
  margin: 44px 0;
}

.projects__cards {
  margin: 0 auto;
  display: grid;
  grid-gap: 1rem;
  justify-content: center;
}

@media (min-width: 1103px) {

  .projects__cards {
    grid-template-columns: repeat(3, 331px);
  }
}

@media screen and (max-width: 1102px) and (min-width: 750px) {
  .projects__cards {
    grid-template-columns: repeat(2, 331px);
  }
}

@media (max-width: 900px) {
